@charset "UTF-8";
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *::before, *::after {
  box-sizing: border-box; }

.container {
  max-width: 1111px;
  margin: 0 auto; }

/*
header 區塊 PC版
*/
.header {
  background-color: #AA0601;
  display: flex; }

.bannerimg {
  height: 49px;
  width: 100%;
  margin: 30px 0 30px 80px; }

.menu {
  display: flex;
  justify-content: end;
  align-items: center; }

.menu-item {
  min-width: 80px;
  margin-right: 80px; }
  .menu-item a {
    color: #fff;
    font-size: 20px;
    text-decoration: none;
    display: block;
    line-height: 30px; }
    .menu-item a:hover {
      color: #DEE2E6; }

.menu-item-last {
  margin-right: 88px; }

/*
footer區塊 PC版
*/
.footer {
  background-color: #AA0601; }

.footerinfo {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.footer-menu {
  display: flex; }

.footer-menu-item {
  margin-right: 48px; }
  .footer-menu-item a {
    color: #fff;
    text-decoration: none;
    line-height: 24px; }
    .footer-menu-item a:hover {
      color: #DEE2E6; }

.soical {
  display: flex;
  align-items: center;
  margin: 32px 0 24px 0; }
  .soical li {
    margin-left: 32px; }

.contactinfo {
  padding-bottom: 21px;
  border-bottom: 1px solid #FFF; }
  .contactinfo li img {
    height: 36px; }
  .contactinfo li a {
    padding-left: 22px;
    color: #fff;
    font-size: 36px;
    line-height: 54px;
    text-decoration: none; }

.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .copyright h3 {
    color: #fff;
    line-height: 24px;
    padding: 20px 0 27px 0; }

.copyright-zh {
  display: flex; }

.copyright-zh-item a {
  color: #fff;
  text-decoration: none;
  padding-left: 48px; }
  .copyright-zh-item a:hover {
    color: #DEE2E6; }
